body, html {
    width: 100%;
    overflow-x: hidden;
}
/*#container {*/
    /*width: 100%;*/
/*}*/
.clear {
    clear: both;
}
header {
    height: 60px;
    width: 100%;
    box-shadow: 0px 2px 8px rgba(0,0,0,0.4);
    position: fixed;
    top: 0px;
    z-index: 100;
    background: #fff;
}
.header_container {
    max-width: 1040px;
    height: 60px;
    margin: 0 auto;
}
.logo {
    float: left;
    width: 160px;
    height: 47px;
    margin-top: 6px;
}
.logo img {
    width: 100%;
    height: 100%;
    display: block;
}
.menu {
    float: right;
    padding-top: 15px;
}
.menu li {
    float: left;
    margin-left: 32px;
}
.menu li a{
    border-bottom: solid 2px #fff;
    height: 30px;
    line-height: 30px;
    display: block;
    font-size: 14px;
    color: #333;
}
.menu li a:hover {
    color: #172991;
    border-bottom-color: #172991;
    font-weight: 600;
}
.menu li a.active {
    color: #172991;
    border-bottom-color: #172991;
    font-weight: 600;
}
.banner {
    /*padding-top: 60px;*/
}
.swiper-container {
    width: 100%;
    height: 600px;
}
.swiper-container img {
    display: block;
    width: 100%;
    height: 100%;
}
