@charset "utf-8";

a,body {
    color: #333
}

button,fieldset,img {
    border: 0
}

* {
    margin: 0;
    padding: 0
}

body,button,h1,h2,h3,h4,h5,h6,input,select,textarea {
    font: 400 26px/1.5 HelveticaNeue-Thin,"Helvetica Neue",Helvetica,Arial,PingFangSC-Light,"PingFang SC","Hiragino Sans GB","Heiti SC","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
    font-style: normal
}

.lowie footer,.lowie header,.lowie nav,address,article,aside,details,figcaption,figure,footer,header,hgroup,iframe,menu,nav,section {
    display: block
}

body {
    -ms-touch-action: none;
    touch-action: none;
    position: relative;
    -moz-user-select: none;
    background: #f3f3f3;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: transparent;
    -webkit-text-size-adjust: none
}

address,cite,dfn,em,i,var {
    font-weight: 400;
    font-style: normal
}

dfn {
    font-family: Arial
}

ol,ul {
    list-style: none
}

address,em {
    font-style: normal
}

a {
    text-decoration: none
}

a:hover {
    -webkit-transition: color .2s linear;
    transition: color .2s linear
}

button,input,select,textarea {
    font-size: 100%;
    outline: 0
}

button {
    outline: 0
}

table {
    border-spacing: 0;
    border-collapse: collapse
}

input {
    border: none;
    border-radius: 0;
    -webkit-appearance: none
}

input::-webkit-input-placeholder,textarea::-webkit-input-placeholder {
    color: #cdcdcd
}

input:-moz-placeholder,textarea:-moz-placeholder {
    color: #cdcdcd
}

input:-ms-input-placeholder,textarea:-ms-input-placeholder {
    color: #cdcdcd
}

#docWrap,body,html {
    width: 100%;
    min-width: 1140px;
    height: 100%;
    background-color: #fff
}

#header {
    height: 60px
}

#header .fix-head {
    position: fixed;
    height: 60px;
    width: 100%;
    background: #fff;
    -webkit-box-shadow: 0 2px 8px rgba(0,0,0,.4);
    box-shadow: 0 2px 8px rgba(0,0,0,.4);
    z-index: 9999
}

#header .fix-head .page-head {
    max-width: 1040px;
    height: 60px;
    margin: 0 auto;
    overflow: hidden;
    position: relative
}

#header .fix-head .page-head .page-logo {
    width: 160px;
    height: 47px;
    margin-top: 6px;
    background-image: url(/images/logo.png);
    background-size: 160px;
    background-repeat: no-repeat
}

#header .fix-head .page-head .nav-list {
    position: absolute;
    right: 0;
    top: 15px;
    width: 50%;
    min-width: 230px;
    z-index: 999;
    background: #fff
}

#header .fix-head .page-head .nav-list .head-nav {
    float: right;
    height: 30px;
    line-height: 30px;
    font-size: 13px;
    border-bottom: 2px solid #fff;
    margin-left: 32px
}

#header .fix-head .page-head .nav-list .head-nav.active,#header .fix-head .page-head .nav-list .head-nav:hover {
    font-weight: 600;
    color: #172991;
    border-bottom-color: #172991
}

#footer,#footer a {
    color: rgba(102,102,102,.6)
}

#header .fix-head .page-head .nav-list .showcooperation {
    width: 30px;
    height: 30px;
    display: block;
    background: url(/images/common/tel.gif) no-repeat
}

#header .fix-head .page-head .nav-list .showcooperation.active,#header .fix-head .page-head .nav-list .showcooperation:hover {
    border: none
}

#footer {
    background: #131616;
    height: 300px;
    font-size: 13px;
    text-align: center;
    font-family: 'PingFang SC'
}

#footer .sub-logo {
    width: 1040px;
    margin: 0 auto 60px;
    height: 90px;
    background-image: url(/images/common/sub_logo@2x.png);
    background-size: auto 34px;
    background-repeat: no-repeat;
    background-position: center center;
    border-bottom: 1px solid #262626
}

#footer .company {
    margin-bottom: 20px
}

#footer a {
    margin: 0 3px
}

.lowie #footer .sub-logo {
    background-image: url(/images/common/sub_logo.png)
}

.cooperation-pop {
    position: fixed;
    top: 39px;
    left: 50%;
    margin-left: 200px;
    z-index: 9999;
    width: 240px;
    padding: 32px 50px 26px;
    border-radius: 6px;
    display: none;
    -webkit-transition: all .8s ease-in-out;
    transition: all .8s ease-in-out;
    background: url(/images/common/hoverbg.png) no-repeat
}

.cooperation-pop .title {
    font-family: PingFangSC-Semibold;
    font-size: 18px;
    color: #1e2b68;
    line-height: 28px
}

.cooperation-pop .content {
    font-size: 13px;
    line-height: 18px;
    color: #848dbb
}

.brandIdea {
    background: url(/images/about/about01.jpg) top no-repeat;
    width: 100%;
    height: 2000px
}

.brandImage {
    background: url(/images/about/about02.jpg) top no-repeat;
    width: 100%;
    height: 1580px
}

.investorProfile {
    background: url(/images/about/about03.jpg) top no-repeat;
    width: 100%;
    height: 1824px
}

.contactUs {
    background: url(/images/about/about04.jpg) center no-repeat;
    width: 100%;
    height: 1540px
}

.employment,.joinUs,.shopInfo {
    width: 100%;
    height: 1580px
}

.joinUs {
    background: url(/images/about/about05.jpg) center no-repeat
}

.employment {
    background: url(/images/about/recruit_info.jpg) center no-repeat
}

.shopInfo {
    background: url(/images/about/shop_info.jpg) center no-repeat
}